import React from "react";
import ReactDOM from "react-dom";

const popup = document.createElement("div");
popup.classList.add("popup");
popup.innerHTML = `
  <div class="content">
    <header>
      <h2>Popup</h2>
      <button class="close">close</button>
    </header>
    <section>
      some text
    </section>
    <footer></footer>
  </div>
`;
popup.querySelector(".close").addEventListener("click", hidePopup);

const titleNode = popup.querySelector("h2");
const contentNode = popup.querySelector("section");
const footerNode = popup.querySelector("footer");
const onHideCallbacks = [];

export function showPopup({
  title,
  content,
  centered = false, // Weather or not content is centered.
  actions = [],
  onHide
}) {
  if (!popup.parentNode) document.body.appendChild(popup);

  titleNode.innerHTML = title || "";

  contentNode.innerHTML = "";
  if (React.isValidElement(content)) {
    ReactDOM.render(content, contentNode);
  } else {
    contentNode.innerHTML = content || "";
  }

  if (centered) {
    contentNode.classList.add("centered");
  } else {
    contentNode.classList.remove("centered");
  }

  if (onHide) onHideCallbacks.push(onHide);

  footerNode.innerHTML = "";
  actions.forEach(({ text, fn }) => {
    const n = document.createElement("button");
    n.innerHTML = text;
    n.addEventListener("click", fn);
    footerNode.appendChild(n);
  });
}

export function hidePopup() {
  if (popup.parentNode) popup.parentNode.removeChild(popup);
  ReactDOM.unmountComponentAtNode(contentNode);
  onHideCallbacks.splice(0).forEach(cb => cb());
}
