import chrono from "chrono-node";

import { Schema, Fragment } from "prosemirror-model";
import { ReplaceStep, ReplaceAroundStep, join } from "prosemirror-transform";
import { Plugin, NodeSelection, Selection } from "prosemirror-state";

import { keymap } from "prosemirror-keymap";
import { gapCursor } from "prosemirror-gapcursor";
import { dropCursor } from "prosemirror-dropcursor";
import { history, undo, redo } from "prosemirror-history";
import {
  chainCommands,
  selectParentNode,
  baseKeymap
} from "prosemirror-commands";

import { InputRule, inputRules } from "prosemirror-inputrules";

import decorations from "./decorations";
import {
  formatDate,
  getZeroBasedNum,
  getDaysInMonthByWeeks,
  monthNumToName
} from "./util";
import {
  splitBlockWithDecorations,
  selectNextHeader,
  selectPrevHeader,
  formatTable
} from "./commands";

// =============================================================================
// Schema

export const schema = new Schema({
  nodes: {
    doc: {
      content: "block+"
    },

    paragraph: {
      content: "inline*",
      group: "block",
      parseDOM: [{ tag: "p" }],
      toDOM() {
        return ["p", 0];
      }
    },

    text: {
      group: "inline",
      toDOM(node) {
        return node.text;
      }
    },

    hard_break: {
      inline: true,
      group: "inline",
      selectable: false,
      parseDOM: [{ tag: "br" }],
      toDOM: () => ["br"]
    }
  }
});

// =============================================================================
// Plugins

const INPUT_RULES = (function() {
  let rules = [
    new InputRule(/<calendar:?(\s\d\d\d\d-\d\d?)?>/, function(
      state,
      match,
      start,
      end
    ) {
      const { paragraph } = state.schema.nodes;
      const today = match[1] ? new Date(...match[1].split("-"), 0) : new Date();

      const nodes = Fragment.from([
        paragraph.create(
          null,
          schema.text(
            `*${monthNumToName[today.getMonth()]} ${today.getFullYear()}*`
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|---------|---------|---------|---------|---------|---------|---------|"
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|Â Â *Mon*Â Â |Â Â *Tue*Â Â |Â Â *Wed*Â Â |Â Â *Thu*Â Â |Â Â *Fri*Â Â |Â Â *Sat*Â Â |Â Â *Sun*Â Â |"
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|---------|---------|---------|---------|---------|---------|---------|"
          )
        ),
        ...getDaysInMonthByWeeks(
          today.getMonth(),
          today.getFullYear()
        ).map(week =>
          paragraph.create(
            null,
            state.schema.text(
              "| " +
                week
                  .map(d =>
                    d ? `Â Â Â ${getZeroBasedNum(d.getDate())}Â Â Â ` : "Â Â Â Â Â Â Â Â "
                  )
                  .join("|Â ") +
                "|"
            )
          )
        ),
        paragraph.create(
          null,
          schema.text(
            "|---------|---------|---------|---------|---------|---------|---------|"
          )
        )
      ]);

      return state.tr.replaceWith(start, end, nodes);
    }),
    new InputRule(/<[^>]+>/, function(state, match, start, end) {
      const parsed = chrono.parse(match[0])[0];
      if (!parsed || parsed.index !== 1) {
        return null;
      }

      let insert = chrono.parseDate(match[0]);
      insert = formatDate(insert);

      return state.tr.insertText(insert, start, end);
    })
  ];

  return inputRules({ rules });
})();

// =============================================================================
// Plugins

export const plugins = [
  INPUT_RULES,

  history(),
  dropCursor(),
  gapCursor(),

  keymap({
    "Mod-z": undo,
    "Shift-Mod-z": redo,
    "Mod-i": selectParentNode, // TODO: use it to kill row

    "Shift-Enter": baseKeymap["Enter"],
    "Ctrl-Enter": baseKeymap["Enter"], // TODO: add table row
    // Ctrl-l -> clone line

    Tab: formatTable,
    Enter: splitBlockWithDecorations,
    ...(/Mac/.test(navigator.platform)
      ? {
          "Mod-Enter": baseKeymap["Enter"],
          "Mod-ArrowDown": selectNextHeader,
          "Mod-ArrowUp": selectPrevHeader
        }
      : {
          "Ctrl-y": redo,
          "Ctrl-ArrowDown": selectNextHeader,
          "Ctrl-ArrowUp": selectPrevHeader
        })
  }),
  keymap(baseKeymap),

  decorations
];
