import React from "react";
import { formatDate } from "./util";

export default function Stats({ todos }) {
  const notDone = todos.filter(item => !item.done);
  const today = formatDate(new Date());

  return (
    <div className="Editor--stats">
      <ul>
        <li>
          You have <b>{notDone.length}</b> of {todos.length} items to do.
        </li>
        <li>
          You have{" "}
          <b>
            {
              notDone.filter(item => item.date && item.date.includes(today))
                .length
            }
          </b>{" "}
          items to do today.
        </li>
      </ul>
    </div>
  );
}
