import React from "react";
import store from "../Storage";
import "./Menu.css";

export default class Menu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isHidden: false,
      search: ""
    };
  }

  handleToggleHidden = () => {
    this.setState({ isHidden: !this.state.isHidden });
  };

  handleSearch = e => {
    debugger;
    this.setState({ search: e.target.value });
  };

  componentDidMount() {
    this.unsubscribe = store.subscribe(() => this.forceUpdate());
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    const { isHidden, search } = this.state;
    const selected = store.getSelectedNote();

    let activeNotes = store.getActiveNotes();
    let inactiveNotes = store.getInactiveNotes();

    if (search) {
      const filterFn = n =>
        n.meta.title.toLowerCase().includes(search.toLowerCase());
      activeNotes = activeNotes.filter(filterFn);
      inactiveNotes = inactiveNotes.filter(filterFn);
    }

    return isHidden ? (
      <section className="MenuTrigger" onClick={this.handleToggleHidden}>
        â¤‡
      </section>
    ) : (
      <section className="Menu">
        <button onClick={() => store.addNewNote()}>+</button>
        <button onClick={this.handleToggleHidden}>Hide</button>
        <hr />
        <input
          type="text"
          placeholder="Search..."
          value={search}
          onChange={this.handleSearch}
        />
        <hr />
        <h3 className="Menu--header">Notes</h3>
        <ul>
          {activeNotes.map(n => (
            <li
              key={n.id}
              onClick={() => store.selectNote(n.id)}
              className={`Menu--item${n === selected ? " active" : ""}`}
            >
              {n.meta.title || "--"}
              <button
                onClick={e => {
                  e.stopPropagation();
                  store.deleteNote(n);
                }}
              >
                X
              </button>
            </li>
          ))}
        </ul>
        <h3 className="Menu--header">Archive</h3>
        <ul>
          {inactiveNotes.map(n => (
            <li
              key={n.id}
              onClick={() => store.selectNote(n.id)}
              className={`Menu--item${n === selected ? " active" : ""}`}
            >
              {n.meta.title || "--"}
              <button
                onClick={e => {
                  e.stopPropagation();
                  store.permanentlyDeleteNote(n);
                }}
              >
                X
              </button>
              <button
                onClick={e => {
                  e.stopPropagation();
                  store.restoreNote(n);
                }}
              >
                ^
              </button>
            </li>
          ))}
        </ul>
        <h3 className="Menu--header">Tags</h3>
        <ul>
          {Object.entries(store.getNotesByTagsAndDate()).map(([tag, notes]) => (
            <li>
              #{tag} ({notes.length})
            </li>
          ))}
        </ul>
      </section>
    );
  }
}
