import React, { useState } from "react";
import PropTypes from "prop-types";

import { msToDuration, durationToStr } from "./util";

// TODO: Adds checked and actual duration
// TODO: automatic playing
// TODO: write stats (for v2)

export class TodoPlayer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      time: new Date(),
      data: [],
      current: -1,
      finished: false,
      automatic: false,
      paused: false
    };
  }

  componentDidMount() {
    this._timer = setInterval(this.forceUpdate.bind(this), 1000);
  }

  componentWillUnmount() {
    clearInterval(this._timer);
  }

  componentDidUpdate() {
    const { todos } = this.props;
    const { time, current, automatic, finished } = this.state;

    if (finished) return;
    if (!automatic) return;

    const currentTime = new Date();
    const currentItem = todos[current];
    const currentDuration = durationToStr(msToDuration(currentTime - time));

    if (currentItem.duration === currentDuration) {
      this.handleNextItem(true);
    }
  }

  handleTogglePause = () => {
    const { paused } = this.state;
    if (paused) {
      this.componentDidMount();
    } else {
      this.componentWillUnmount();
    }
    this.setState({
      paused: !paused,
      ...(paused
        ? {
            time: new Date()
          }
        : {})
    });
  };

  handleStart = automatic => {
    this.setState({
      automatic,
      current: 0,
      time: new Date()
    });
  };

  handleNextItem = done => {
    const { todos } = this.props;
    const { current, data, time } = this.state;

    const nextItem = current + 1;
    const nextTime = new Date();

    this.setState({
      time: nextTime,
      data: [
        ...data,
        {
          done,
          time: nextTime - time
        }
      ],
      current: nextItem,
      finished: nextItem === todos.length
    });
  };

  getCurrentItem() {
    return this.props.todos[this.state.current];
  }

  render() {
    const { todos, onFinish } = this.props;
    const { current, data, time, finished, automatic, paused } = this.state;

    const currentTime = new Date();
    const currentItem = todos[current];
    const currentDuration = durationToStr(msToDuration(currentTime - time));

    const isNoDurationItem = todos.find(item => !item.duration);

    if (finished) {
      return (
        <div>
          {data.filter(d => d.done).length} / {data.length} items done.
          <button onClick={() => onFinish(data)}>Close</button>
        </div>
      );
    }

    if (!currentItem) {
      return (
        <div style={{ textAlign: "left" }}>
          <p>
            <button onClick={() => this.handleStart(false)}>Play Manual</button>{" "}
            Manually switch tasks.
          </p>

          <br />
          <br />
          <p>
            <button
              onClick={() => this.handleStart(true)}
              disabled={isNoDurationItem}
            >
              Play Automatic
            </button>{" "}
            Automatically switch task when its duration is over.{" "}
            <i>
              Every task should have duration in order to play automatically.
            </i>
          </p>
        </div>
      );
    }

    return (
      <div>
        <div className="TodoPlayer--text">{currentItem.text}</div>
        <div className="TodoPlayer--time">
          {currentDuration}
          {currentItem.duration && ` / ${currentItem.duration}`}
        </div>
        <div className="TodoPlayer--footer">
          {automatic ? (
            <>
              <button onClick={this.handleTogglePause}>
                {paused ? "Resume" : "Pause"}
              </button>
            </>
          ) : (
            <>
              <button onClick={() => this.handleNextItem(false)}>Skip</button>
              <button onClick={() => this.handleNextItem(true)}>Done</button>
            </>
          )}
        </div>
      </div>
    );
  }
}

TodoPlayer.propTypes = {
  todos: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string.isRequired,
      date: PropTypes.string.isRequired,
      duration: PropTypes.string.isRequired
    }).isRequired
  ),
  onFinish: PropTypes.func.isRequired
};
