const LOCAL_STORAGE_KEY = "__prose-editor-state__";

// TODO: store each note individually
// TODO: throttle writes
// TODO: S3 for key-value storage of files
// TODO: S3 for authentication

export class Storage {
  constructor() {
    this.handlers = [];
    this.state = window.localStorage.getItem(LOCAL_STORAGE_KEY)
      ? JSON.parse(window.localStorage.getItem(LOCAL_STORAGE_KEY))
      : {
          notes: [],
          selectedNote: null
        };

    if (this.state.notes.length === 0) {
      this.addNewNote();
    }

    if (!this.state.selectedNote || !this.getSelectedNote()) {
      this.selectNote(this.state.notes[0].id);
    }
  }

  subscribe(f) {
    this.handlers.push(f);
    return () => {
      this.handlers = this.handlers.filter(fn => fn !== f);
    };
  }

  setState(state) {
    const newState = {
      ...this.state,
      ...state
    };

    this.state = newState;
    window.localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(newState));
    this.handlers.forEach(h => h());
  }

  // ===========================================================================
  // Selectors

  getActiveNotes() {
    return this.state.notes.filter(n => !n.deleted_at);
  }

  getInactiveNotes() {
    return this.state.notes.filter(n => !!n.deleted_at);
  }

  getNextNoteId() {
    return Math.max(...this.state.notes.map(n => n.id), 0) + 1;
  }

  getSelectedNote() {
    const { selectedNote, notes } = this.state;
    return selectedNote && notes.find(n => n.id === selectedNote);
  }

  getNotesByTagsAndDate() {
    const result = {};

    this.getActiveNotes()
      .sort((a, b) => a.updated_at - b.updated_at)
      .forEach(note => {
        note.meta.tags.forEach(tag => {
          if (!result[tag]) result[tag] = [];
          result[tag].push(note);
        });
      });

    return result;
  }

  // ===========================================================================
  // Mutations

  addNewNote = () => {
    const id = this.getNextNoteId();
    this.setState({
      notes: [
        ...this.state.notes,
        {
          id,
          content: null,
          meta: {
            title: null,
            tags: []
          },
          created_at: new Date(),
          updated_at: new Date(),
          deleted_at: null
        }
      ],
      selectedNote: id
    });
  };

  selectNote = id => {
    if (this.state.notes.find(n => n.id === id)) {
      this.setState({ selectedNote: id });
    } else {
      throw new Error(`Cannot find note with id: ${id}`);
    }
  };

  updateNote = note => {
    this.setState({
      notes: this.state.notes.map(n => {
        if (n.id !== note.id) {
          return n;
        }

        return {
          ...n,
          ...note,
          updated_at: new Date()
        };
      })
    });
  };

  deleteNote = note => {
    this.updateNote({
      ...note,
      deleted_at: new Date()
    });
  };

  restoreNote = note => {
    this.updateNote({
      ...note,
      deleted_at: null
    });
  };

  permanentlyDeleteNote = note => {
    const { notes, selectedNote } = this.state;
    const remainingNotes = notes.filter(n => n !== note);
    this.setState({
      notes: remainingNotes,
      selectedNote:
        selectedNote === note.id ? remainingNotes[0].id : selectedNote
    });
  };
}

const instance = new Storage();
export default instance;
