import Agenda from "./Agenda";
import Stats from "./Stats";
import CheatSheet from "../CheatSheet/CheatSheet";

import store from "../Storage";

import { schema, plugins } from "./setup";
import { isToday, formatDate } from "./util";

import "./Editor.css";

class Editor extends React.Component {
  constructor(props) {
    super(props);

    this.elm = null;

    this._editorView = null;
    this._editorState = null;

    this.note = store.getSelectedNote();
    this.unsubscribe = store.subscribe(() => {
      const selected = store.getSelectedNote();
      if (this.note.id !== selected.id) {
        this.note = selected;
        this.forceUpdate();
      }
    });

    this.state = {
      helpShown: false,
      agendaShown: false,
      statsShown: false
    };
  }

  showEditor = () => {
    this.setState({
      helpShown: false,
      agendaShown: false,
      statsShown: false
    });
  };
  toggleHelp = () =>
    this.setState({
      helpShown: !this.state.helpShown,
      agendaShown: false,
      statsShown: false
    });
  toggleStats = () =>
    this.setState({
      statsShown: !this.state.statsShown,
      helpShown: false,
      agendaShown: false
    });
  toggleAgenda = () =>
    this.setState({
      agendaShown: !this.state.agendaShown,
      helpShown: false,
      statsShown: false
    });

  componentDidMount() {
    document.addEventListener(
      "keydown",
      e => {
        // Prevent default tab behavior.
        if (e.keyCode === 9) {
          e.preventDefault();
          return;
        }

        // Allow clicking links when pressing control.
        if (e.keyCode === 91) {
          [...document.querySelectorAll("a")].forEach(
            a => (a.contentEditable = false)
          );
        }
      },
      false
    );

    document.addEventListener(
      "keyup",
      e => {
        if (e.keyCode === 91) {
          [...document.querySelectorAll("a")].forEach(
            a => (a.contentEditable = true)
          );
        }
      },
      false
    );
  }

  componentWillUnmount() {
    if (this._editorView) {
      this._editorView.destroy();
    }

    this.unsubscribe();
  }

  _createEditorView = element => {
    if (element != null) {
      this.elm = element;
      this._editorState = this.note.content
        ? EditorState.fromJSON(
            {
              schema,
              plugins
            },
            this.note.content
          )
        : EditorState.create({
            schema,
            plugins
          });

      this._editorView = new EditorView(element, {
        state: this._editorState,
        dispatchTransaction: tx => {
          this._editorState = this._editorState.apply(tx);
          this._editorView.updateState(this._editorState);

          store.updateNote({
            ...this.note,
            content: this._editorState.toJSON(),
            meta: {
              title: this.getTitle(),
              tags: this.getTags()
            }
          });
        }
      });
    }
  };

  // ===========================================================================
  // Commands

  getTitle() {
    const heading = this.elm && this.elm.querySelector(".heading p");
    return heading
      ? heading.lastChild.textContent.trim().replace(/#+/g, "")
      : "--";
  }

  getTodos() {
    return [...this.elm.querySelectorAll(".todo")]
      .map(n => n.parentNode)
      .map(n => {
        const date = n.querySelector(".date");
        const duration = n.querySelector(".duration");

        const clone = n.cloneNode(true);
        Array.from(clone.children)
          .filter(c => c.classList.contains("ProseMirror-widget"))
          .forEach(c => c.parentNode.removeChild(c));

        const remove = el => el && el.parentNode.removeChild(el);
        remove(clone.querySelector(".date"));
        remove(clone.querySelector(".duration"));

        return {
          text: clone.innerText.replace(/^(\-|\+)\s/, ""),
          date: date && date.innerText,
          done: clone.innerText[0] === "+",
          duration: duration && duration.innerText,
          toggle: () => n.querySelector("input[type=checkbox]").click()
        };
      });
  }

  getTags() {
    const tags = this.elm && this.elm.querySelectorAll(".tag");
    return tags ? [...tags].map(t => t.textContent.slice(1)) : [];
  }

  getSummary() {}

  // ===========================================================================
  // Handlers

  // TODO: Example of commands for menu or something
  onFocus() {
    if (this._editorView) {
      this._editorView.focus();
    }
  }

  // ===========================================================================
  // Rendering

  renderHelp() {
    return (
      <div className="Editor--help">
        <CheatSheet />
      </div>
    );
  }

  renderMenu() {
    const { statsShown, agendaShown, helpShown } = this.state;
    const hideEdditor = statsShown || agendaShown || helpShown;
    return (
      <div className="Editor--menu">
        <button className={!hideEdditor && "active"} onClick={this.showEditor}>
          Editor
        </button>
        <button className={agendaShown && "active"} onClick={this.toggleAgenda}>
          Agenda
        </button>
        <button className={statsShown && "active"} onClick={this.toggleStats}>
          Stats
        </button>
        <button className={helpShown && "active"} onClick={this.toggleHelp}>
          Help
        </button>
      </div>
    );
  }

  render() {
    const { statsShown, agendaShown, helpShown } = this.state;
    const hideEdditor = statsShown || agendaShown || helpShown;
    return (
      <div className="Editor">
        {this.renderMenu()}
        {statsShown && <Stats todos={this.getTodos()} />}
        {agendaShown && <Agenda todos={this.getTodos()} />}
        {helpShown && this.renderHelp()}
        <div
          className="EditorWrapper"
          key={this.note.id}
          ref={this._createEditorView}
          style={hideEdditor ? { display: "none" } : {}}
        />
      </div>
    );
  }
}

export default Editor;
