import React from "react";
import {
  parseDuration,
  normalizeDuration,
  formatDate,
  getZeroBasedNum,
  durationToStr
} from "./util";

const dateRegEx = /(\d\d\d\d)-(\d\d)-(\d\d)(:?\s(\d\d):(\d\d))?/;
const durationRegEx = /\b(\d\d?[hms])(\d\d?[ms])?(\d\d?s)?\b/;

function processTodos(todos) {
  const today = formatDate(new Date());
  const forToday = todos
    .filter(item => item.date && item.date.includes(today))
    .map(item => ({
      ...item,
      dateMatch: item.date.match(dateRegEx)
    }));

  if (forToday.length === 0) {
    return null;
  }

  const noHour = forToday.filter(item => {
    const timeData = item.dateMatch[4];
    return !timeData;
  });

  const withHour = forToday
    .filter(item => !!item.dateMatch[4])
    .sort((a, b) => {
      const [hoursA, minutesA] = a.dateMatch.slice(5);
      const [hoursB, minutesB] = b.dateMatch.slice(5);
      return hoursA * 60 + minutesA * 1 - (hoursB * 60 + minutesB * 1);
    });

  return { withHour, noHour };
}

export default function Agenda({ todos }) {
  const groups = processTodos(todos);

  if (!groups) {
    return (
      <div className="Editor--agenda">
        <h3>Agenda</h3>
        ----
      </div>
    );
  }
  const { withHour, noHour } = groups;

  let prevTime = "00:00";

  return (
    <div className="Editor--agenda">
      <h3>Agenda</h3>
      <ul>
        {withHour.map((item, i) => {
          const [hours, minutes] = item.dateMatch.slice(5);
          const atTime = `${hours}:${minutes}`;

          let toTime = "";
          if (item.duration) {
            const d = parseDuration(item.duration);

            d.h += hours * 1;
            d.m += minutes * 1;

            normalizeDuration(d);

            toTime = `${getZeroBasedNum(d.h)}:${getZeroBasedNum(d.m)}`;
          }

          let spaceTime = null;
          if (prevTime !== atTime) {
            spaceTime = (
              <li key={prevTime}>
                <b>
                  [{prevTime}-{atTime}]
                </b>
                : ---------------------
              </li>
            );
          }
          prevTime = toTime || atTime;
          return (
            <>
              {spaceTime}
              <li key={i}>
                <b>
                  [{atTime}
                  {toTime ? `-${toTime}` : ""}]
                </b>{" "}
                <input
                  type="checkbox"
                  defaultChecked={item.done}
                  onClick={item.toggle}
                />
                {item.text}
              </li>
            </>
          );
        })}
        {prevTime !== "23:59" && (
          <li key="last">
            <b>[{prevTime}-23:59]</b>: ---------------------
          </li>
        )}
      </ul>

      <h3>Task for the day</h3>
      <ul>
        {noHour.map((item, i) => (
          <li key={i}>
            <input
              type="checkbox"
              defaultChecked={item.done}
              onClick={item.toggle}
            />
            {item.text}
          </li>
        ))}
      </ul>
    </div>
  );
}
