import Pikaday from "pikaday";
import hljs from "highlight.js";

import { Plugin } from "prosemirror-state";
import { Decoration, DecorationSet } from "prosemirror-view";
import { formatDate } from "./util";
import { TODOIndicator, DurationWidget } from "./indicators";

import "pikaday/css/pikaday.css";
import "highlight.js/styles/arta.css";

/**
 * Decorations that require the same open and close strings
 * and capture all the lines in between.
 *
 * NOTE: These work on the wrapper node, not on the text node.
 *
 */
export const OpenCloseDecorations = {
  code: /^```/gi
};

export const WholeNodeDecorations = {
  // NOTE: Table row is also handled repeatably.
  tableRow: /^\|(?:\s|\-|=).*$/gi,
  _heading: /^(#{1,5})\s.*$/gi // Underscored names are skipped.
};

export const RepeatableDecorations = {
  quote: /^>\s/gi,
  bullet: /^\*\s/gi,
  ordered: /^\d+\.\s/gi,
  todo: /^(\-|\+)\s/gi
};

/**
 * These work on the text node.
 */
export const Decorations = {
  bold: /\*\w[\w\s]*\w\*/gi,
  italic: /\/\w[\w\s]*\w\//gi,
  highlight: /(\`[^\`]+\`)|important|now|TODO:|NOTE:/gi,
  underline: /\_[^\_\s]+\_/gi,
  striketrough: /\~[^\~\s]+\~/gi,
  tag: /#[^\b\s#]+/gi,
  hr: /^\-\-\-$/gi,
  url: /https?:\/\/[a-z0-9\.]+/gi,
  explication: /\([^\)]+\)/gi,

  date: /\b(\d\d\d\d)-(\d\d)-(\d\d)(:?\s(\d\d):(\d\d))?\b/gi,
  duration: /\b(\d\d?[hms])(\d\d?[ms])?(\d\d?s)?\b/gi,

  // NOTE: If name starts with `_`, standard decoration won't be
  // created. This can be used if we some special handling in
  // with DecorationsWidgets function.
  _counter: /\[(\d+)\]/gi,
  _checkbox: /\[(\+|\-)\]\s/gi,
  _proportion: /\[(\d+)\/(\d+)\]/gi,

  ...RepeatableDecorations
};

/**
 * Some decorations require additional widgets.
 */
export const DecorationsWidgets = {
  todo: getTodoWidgets,
  duration: getDurationWidgets,
  date: getDateWidgets,
  _checkbox: getCheckboxWidgets,
  _counter: getCounterWidgets,
  _proportion: getCounterWidgets,
  _heading: getHeadingWidgets
};

/**
 * Decoration won't be visible inside specified nodes.
 */
const excludedNodes = {};

/**
 * We don't want some decorations in table rows.
 */
const excludedInTableRow = {
  _counter: true,
  _checkbox: true,
  _proportion: true,
  date: true,
  duration: true
};

function getDecorations(doc) {
  let decos = [];
  let openClose = {};

  doc.descendants((node, pos) => {
    if (excludedNodes[doc.resolve(pos).node().type.name]) return;

    if (node.isText) {
      const isTableRow = !!node.text.match(WholeNodeDecorations["tableRow"]);
      Object.keys(Decorations).forEach(name => {
        const regex = Decorations[name];
        while (true) {
          let m = regex.exec(node.text);
          if (!m) return;

          if (excludedInTableRow[name] && isTableRow) {
            return;
          }

          if (name[0] !== "_") {
            decos.push(
              Decoration.inline(pos + m.index, pos + m.index + m[0].length, {
                class: name,

                ...(name === "url"
                  ? {
                      nodeName: "a",
                      href: node.text,
                      title: node.text
                    }
                  : {})
              })
            );
          }

          if (DecorationsWidgets[name]) {
            decos.push(...DecorationsWidgets[name](m, pos, doc, node));
          }
        }
      });

      return;
    }

    Object.keys(WholeNodeDecorations).forEach(name => {
      const regex = WholeNodeDecorations[name];
      while (true) {
        let m = regex.exec(node.textContent);
        if (!m) return;

        if (name[0] !== "_") {
          decos.push(
            Decoration.node(pos + m.index, pos + m.index + node.nodeSize, {
              class: name
            })
          );
        }

        if (DecorationsWidgets[name]) {
          decos.push(...DecorationsWidgets[name](m, pos, doc, node));
        }
      }
    });

    Object.keys(OpenCloseDecorations).forEach(name => {
      const regex = OpenCloseDecorations[name];
      while (true) {
        let m = regex.exec(node.textContent);
        if (!m) return;

        if (!openClose[name]) {
          openClose[name] = { node, pos };
          continue;
        }

        if (openClose[name]) {
          const closing = { node, pos };
          const opening = openClose[name];

          const textContent = [];
          doc.nodesBetween(opening.pos, closing.pos + 1, (n, p) => {
            if (n.isText) return;
            textContent.push(n.textContent);

            decos.push(
              Decoration.node(p, p + n.nodeSize, {
                class: name
              })
            );
          });

          delete openClose[name];

          decos.push(
            Decoration.widget(
              opening.pos + 1,
              (view, getPos) => {
                const dom = document.createElement("pre");
                const lang = textContent[0].replace("```", "");
                const source = hljs.getLanguage(lang)
                  ? hljs.highlight(lang, textContent.slice(1, -1).join("\n"))
                      .value
                  : textContent.slice(1, -1).join("\n");
                dom.innerHTML = `${textContent[0]}\n${source}\n${
                  textContent[textContent.length - 1]
                }`;

                dom.classList.add("code-highlight");

                return dom;
              },
              {
                key: textContent.join("\n")
              }
            )
          );
          continue;
        }
      }
    });
  });

  return DecorationSet.create(doc, decos);
}

// =============================================================================
// Widgets

function getTodoWidgets(m, pos, doc) {
  const decos = [];
  const currentNode = doc.resolve(pos).node();
  const nodeBefore = doc.resolve(pos - 1).nodeBefore;
  const nodeAfter = doc.resolve(pos + currentNode.nodeSize).node();

  decos.push(
    Decoration.widget(
      pos + m.index,
      (view, getPos) => {
        const dom = document.createElement("input");
        dom.style.zIndex = 2;
        dom.style.position = "relative";
        dom.type = "checkbox";
        dom.checked = m[1] === "+";
        dom.addEventListener("click", e => {
          view.dispatch(
            view.state.tr.insertText(
              m[1] === "+" ? "-" : "+",
              getPos(),
              getPos() + 1
            )
          );
        });
        return dom;
      },
      {
        key: m[0]
      }
    )
  );

  if (
    (!nodeBefore || !/(\+|\-)\s/.test(nodeBefore.textContent.slice(0, 2))) &&
    nodeAfter &&
    /(\+|\-)\s/.test(nodeAfter.textContent.slice(0, 2))
  ) {
    decos.push(
      // TODO: cache
      Decoration.widget(pos + m.index, view => TODOIndicator(view), {
        key: currentNode.textContent
      })
    );
  }

  return decos;
}

function getDurationWidgets(m, pos) {
  return [
    Decoration.widget(
      pos + m.index,
      (view, getPos) => DurationWidget(m, view, getPos),
      {
        key: m[0]
      }
    )
  ];
}

function getDateWidgets(m, pos) {
  return [
    Decoration.widget(
      pos + m.index,
      (view, getPos) => {
        const dom = document.createElement("span");
        dom.classList.add("date--menu");
        dom.innerHTML = `
          <button class="date--menu--trigger">â–¼</button>
        `;

        const input = document.createElement("input");
        const currentDate = new Date(Date.parse(m[0]));
        input.value = formatDate(currentDate);

        // TODO: https://github.com/fengyuanchen/pickerjs
        const picker = new Pikaday({
          field: input,
          trigger: dom.querySelector("button.date--menu--trigger"),
          firstDay: 1,
          defaultDate: currentDate,
          onSelect: function() {
            debugger;
            view.dispatch(
              view.state.tr.insertText(
                formatDate(this.getDate()),
                getPos(),
                getPos() + m[0].length
              )
            );
          }
        });

        return dom;
      },
      {
        key: m[0]
      }
    )
  ];
}

function getCheckboxWidgets(m, pos) {
  return [
    Decoration.inline(pos + m.index, pos + m.index + m[0].length - 1, {
      class: "checkbox",
      contenteditable: false
    }),
    Decoration.widget(
      pos + m.index,
      (view, getPos) => {
        const dom = document.createElement("input");
        dom.style.zIndex = 2;
        dom.style.position = "relative";
        dom.type = "checkbox";
        dom.checked = m[1] === "+";
        dom.addEventListener("click", e => {
          view.dispatch(
            view.state.tr.insertText(
              m[1] === "+" ? "-" : "+",
              getPos() + 1,
              getPos() + 2
            )
          );
        });
        return dom;
      },
      {
        key: m[0]
      }
    )
  ];
}

function getCounterWidgets(m, pos) {
  return [
    Decoration.inline(pos + m.index, pos + m.index + 1, {
      class: "counter--hidden"
    }),
    Decoration.widget(
      pos + m.index,
      (view, getPos) => {
        const dom = document.createElement("button");
        dom.className = "counter--left";
        dom.innerHTML = "-";

        dom.addEventListener("mousedown", e => {
          e.preventDefault();
          e.stopPropagation();
        });
        dom.addEventListener("click", e => {
          e.stopPropagation();
          e.preventDefault();
          view.dispatch(
            view.state.tr.insertText(
              `[${Math.max(m[1] * 1 - 1, 0)}${m[2] ? `/${m[2]}` : ""}]`,
              getPos(),
              getPos() + m[0].length
            )
          );
        });
        return dom;
      },
      {
        key: m[0]
      }
    ),
    Decoration.inline(pos + m.index + 1, pos + m.index + m[0].length - 1, {
      class: "counter"
    }),
    Decoration.inline(
      pos + m.index + m[0].length - 1,
      pos + m.index + m[0].length,
      {
        class: "counter--hidden right"
      }
    ),
    Decoration.widget(
      pos + m.index + m[0].length - 1,
      (view, getPos) => {
        const dom = document.createElement("button");
        dom.className = "counter--right";
        dom.innerHTML = "+";

        dom.addEventListener("mousedown", e => {
          e.preventDefault();
          e.stopPropagation();
        });

        dom.addEventListener("click", e => {
          e.preventDefault();
          view.dispatch(
            view.state.tr.insertText(
              `[${Math.min(m[1] * 1 + 1, m[2] ? m[2] : Infinity)}${
                m[2] ? `/${m[2]}` : ""
              }]`,
              getPos() - m[0].length + 1,
              getPos() + 1
            )
          );
        });
        return dom;
      },
      {
        key: m[0]
      }
    )
  ];
}

function getHeadingWidgets(m, pos, _, node) {
  return [
    Decoration.node(pos + m.index, pos + m.index + node.nodeSize, {
      class: "heading",
      nodeName: `h${m[1].length}`
    }),
    Decoration.inline(pos + m.index + 1, pos + m.index + 1 + m[1].length, {
      class: "heading--hidden",
      contenteditable: false
    }),
    Decoration.widget(
      pos + m.index + 1,
      (view, getPos) => {
        const dom = document.createElement("div");
        dom.classList.add("indicator");
        dom.innerHTML = `H${m[1].length}`;

        dom.addEventListener("click", e => {
          e.preventDefault();
          view.dispatch(
            view.state.tr.insertText(
              m[1].length === 5 ? "#" : `${m[1]}#`,
              getPos(),
              getPos() + m[1].length
            )
          );
        });
        return dom;
      },
      {
        key: m[0]
      }
    )
  ];
}

