import React from "react";

import { NodeSelection } from "prosemirror-state";
import { showPopup, hidePopup } from "./popup";
import { durationToStr, normalizeDuration, parseDurationMatch } from "./util";
import { TodoPlayer } from "./TodoPlayer";

export const TODOIndicator = view => {
  const dom = document.createElement("div");
  dom.classList.add("indicator");
  dom.classList.add("todo-indicator");
  dom.innerHTML = `
    <button class="trigger">
      ...
    </button>
    <div class="options">
      <button data-action="start">Start</button>
      <button data-action="reset">Reset</button>
      <button data-action="delete_completed">Delete Completed</button>
      <button data-action="delete_unfinished">Delete Unfinished</button>
      <button data-action="copy">Copy list</button>
    </div>
  `;

  dom.addEventListener("click", e => {
    e.preventDefault();
    const node = e.target;
    const action = node.dataset.action;

    const fullTodoList = [dom.parentNode];

    let next;
    while (
      (next = fullTodoList[fullTodoList.length - 1]) &&
      (next = next.nextElementSibling) &&
      !!next.querySelector(".todo")
    ) {
      fullTodoList.push(next);
    }

    switch (action) {
      case "start": {
        const parsedNodes = fullTodoList.map(n => {
          const date = n.querySelector(".date");
          const duration = n.querySelector(".duration");

          const clone = n.cloneNode(true);
          Array.from(clone.children)
            .filter(c => c.classList.contains("ProseMirror-widget"))
            .forEach(c => c.parentNode.removeChild(c));

          return {
            text: clone.innerText.replace(/^(\-|\+)\s/, ""),
            date: date && date.innerText,
            duration: duration && duration.innerText
          };
        });

        const onFinish = data => {
          data.forEach(({ done, time }, i) => {
            const node = fullTodoList[i];
            const checkbox = node.querySelector(
              "input[type=checkbox]:not(:checked)"
            );

            if (done) {
              checkbox.click();
            }
          });
          hidePopup();
        };
        showPopup({
          content: <TodoPlayer todos={parsedNodes} onFinish={onFinish} />,
          centered: true
        });

        return;
      }

      case "reset": {
        fullTodoList
          .map(item => item.querySelector("input[type=checkbox]:checked"))
          .filter(Boolean)
          .forEach(item => item.click());
        return;
      }
      case "delete_unfinished":
      case "delete_completed": {
        const nodes = fullTodoList
          .filter(
            item =>
              !!item.querySelector(
                `input[type=checkbox]${
                  action === "delete_unfinished" ? ":not(:checked)" : ":checked"
                }`
              )
          )
          .reverse() // If we don't delete in reverse, prosemirror removes nodes
          .forEach(item => {
            if (!item.parentNode) return;

            const pos = view.posAtDOM(item);
            const n = view.state.doc.nodeAt(pos);
            const tr = view.state.tr.deleteRange(pos - 2, pos + n.nodeSize);
            view.dispatch(tr);
          });
      }
      case "copy": {
        return;
      }
      default:
        return;
    }
  });
  return dom;
};

export const DurationWidget = (match, view, getPos) => {
  const dom = document.createElement("span");
  dom.classList.add("duration--menu");
  dom.innerHTML = `
        <button class="duration--menu--trigger">â–¼</button>
        <div class="duration--menu--options">
          <button data-action="play">Play</button>
          <button data-action="start">Continue</button>
          <button data-action="double">Double</button>
        </div>
      `;

  dom.addEventListener("click", e => {
    switch (e.target.dataset.action) {
      case "start": {
        const d = parseDurationMatch(match);
        const popupProps = {
          centered: true,
          actions: [
            {
              text: "Stop",
              fn: hidePopup
            }
          ]
        };
        let interval = setInterval(() => {
          d.s += 1;
          normalizeDuration(d);
          showPopup({ content: durationToStr(d), ...popupProps });
        }, 1000);

        const prevText = durationToStr(d);
        showPopup({
          ...popupProps,
          content: durationToStr(d),
          onHide: () => {
            clearInterval(interval);
            view.dispatch(
              view.state.tr.insertText(
                durationToStr(d),
                getPos(),
                getPos() + prevText.length
              )
            );
          }
        });
        return;
      }
      case "double": {
        const m = parseDurationMatch(match);

        Object.keys(m).forEach(k => {
          m[k] = m[k] * 2;
        });

        normalizeDuration(m);

        const text = durationToStr(m);
        view.dispatch(
          view.state.tr.insertText(text, getPos(), getPos() + match[0].length)
        );
        return;
      }

      case "play": {
        const target = durationToStr(parseDurationMatch(match));
        const d = parseDurationMatch([]);
        const popupProps = {
          centered: true,
          actions: [
            {
              text: "Stop",
              fn: hidePopup
            }
          ]
        };
        let interval = setInterval(() => {
          d.s += 1;
          normalizeDuration(d);
          const current = durationToStr(d);
          showPopup({
            content: `${current}/${target}`,
            ...popupProps
          });

          if (current === target) {
            clearInterval(interval);
            showPopup({
              ...popupProps,
              content: `${current}/${target}`,
              actions: [
                {
                  text: "End",
                  fn: hidePopup
                }
              ]
            });
          }
        }, 1000);

        const prevText = durationToStr(d);
        showPopup({
          ...popupProps,
          content: durationToStr(d),
          onHide: () => {
            clearInterval(interval);
            // view.dispatch(
            //   view.state.tr.insertText(
            //     durationToStr(d),
            //     getPos(),
            //     getPos() + prevText.length
            //   )
            // );
          }
        });
        return;
      }
      default:
    }
  });
  return dom;
};
