import {
  joinPoint,
  canJoin,
  findWrapping,
  liftTarget,
  canSplit,
  ReplaceAroundStep
} from "prosemirror-transform";
import { Slice, Fragment } from "prosemirror-model";
import {
  Selection,
  TextSelection,
  NodeSelection,
  AllSelection,
  SelectionRange
} from "prosemirror-state";

import { RepeatableDecorations, WholeNodeDecorations } from "./decorations";

// =============================================================================
// Lists repeating

export function splitBlockWithDecorations(state, dispatch) {
  let { $from, $to } = state.selection;
  if (
    state.selection instanceof NodeSelection &&
    state.selection.node.isBlock
  ) {
    if (!$from.parentOffset || !canSplit(state.doc, $from.pos)) return false;
    if (dispatch) dispatch(state.tr.split($from.pos).scrollIntoView());
    return true;
  }

  if (!$from.parent.isBlock) return false;

  if (dispatch) {
    let atEnd = $to.parentOffset == $to.parent.content.size;
    let tr = state.tr;
    if (state.selection instanceof TextSelection) tr.deleteSelection();
    let deflt =
      $from.depth == 0
        ? null
        : $from.node(-1).contentMatchAt($from.indexAfter(-1)).defaultType;
    let types = atEnd && deflt ? [{ type: deflt }] : null;
    let can = canSplit(tr.doc, tr.mapping.map($from.pos), 1, types);
    if (
      !types &&
      !can &&
      canSplit(tr.doc, tr.mapping.map($from.pos), 1, deflt && [{ type: deflt }])
    ) {
      types = [{ type: deflt }];
      can = true;
    }
    if (can) {
      tr.split(tr.mapping.map($from.pos), 1, types);
      if (
        !atEnd &&
        !$from.parentOffset &&
        $from.parent.type != deflt &&
        $from
          .node(-1)
          .canReplace(
            $from.index(-1),
            $from.indexAfter(-1),
            Fragment.from(deflt.create(), $from.parent)
          )
      )
        tr.setNodeMarkup(tr.mapping.map($from.before()), deflt);
    }

    const { textContent } = $from.parent;
    Object.values(RepeatableDecorations).forEach(d => {
      let match;
      if (!(match = textContent.match(d))) {
        return;
      }

      if (match[0] === textContent) {
        tr.delete($from.pos - 2, $from.pos + 2);
      } else {
        let text = match[0];
        if (match[0] == "+ ") {
          text = "- ";
        } else if (RepeatableDecorations.ordered.test(match[0])) {
          text = `${match[0][0] * 1 + 1}${match[0].slice(1)}`;
        }

        tr.insertText(text.replace(" ", `\xa0`));
      }
    });

    if (WholeNodeDecorations.tableRow.test(textContent)) {
      tr.insertText("| ".replace(" ", `\xa0`));
    }

    dispatch(tr.scrollIntoView());
  }
  return true;
}

// =============================================================================
// Tables

export function formatTable(state, dispatch, view) {
  const { $from, to } = state.selection;
  const same = $from.sharedDepth(to);
  if (same == 0) return false;

  const proseNode = $from.before(same);
  const currentNode = view.nodeDOM(proseNode);
  const domNodes = [currentNode];

  if (!currentNode.classList.contains("tableRow")) return false;

  let next = currentNode;
  while (
    (next = next.nextElementSibling) &&
    next.classList.contains("tableRow")
  ) {
    domNodes.push(next);
  }

  let prev = currentNode;
  while (
    (prev = prev.previousElementSibling) &&
    prev.classList.contains("tableRow")
  ) {
    domNodes.unshift(prev);
  }

  const rows = domNodes.map(n => n.innerText);

  const [result, nextSelectedRow, nextSelectedCell] = getFormattedTable(
    rows,
    domNodes.indexOf(currentNode),
    $from.pos - proseNode - 1
  );

  domNodes.forEach((node, i) => {
    const text = result[i].replace(new RegExp(" ", "g"), `\u00a0`);
    const pos = view.posAtDOM(node);
    const n = view.state.doc.nodeAt(pos);

    const tr = view.state.tr.insertText(text, pos, pos + n.nodeSize);
    if (i === nextSelectedRow) {
      let posInCell = text
        .split("|")
        .slice(0, nextSelectedCell + 2)
        .join("|")
        .trim().length;

      if (posInCell === 1) {
        // Give some space if it is first empty cell.
        posInCell = 2;
      }

      tr.setSelection(
        TextSelection.create(tr.doc, pos + posInCell, pos + posInCell - 2)
      );
    }

    dispatch(tr);
  });
}

function getFormattedTable(rows, selectedRow, selectedChar) {
  const isLineRow = row => row.slice(0, 2) === "|-";

  const dataRows = rows
    .filter(row => !isLineRow(row))
    .map(row =>
      row
        .replace(/\s\s*/g, " ")
        .trim()
        .split("|")
    );

  const separatorRowsIdxs = rows
    .map((row, i) => (isLineRow(row) ? i : null))
    .filter(index => index !== null);

  const maxCellsNum = Math.max(...dataRows.map(r => r.length));
  const table = dataRows.map(row => {
    const extraEmptyCells = " ".repeat(maxCellsNum - row.length).split(" ");
    return [...row, ...extraEmptyCells];
  });

  for (let i = 0; i < maxCellsNum; i++) {
    const maxWidth = Math.max(...table.map(row => row[i].length));
    table.forEach(row => (row[i] = padBothToLength(row[i], maxWidth, " ")));
  }

  const outputTable = table.map(
    row => `| ${row.filter((c, i) => c !== "").join(" | ")} |`
  );

  const lineRow = [...outputTable[0]].map(c => (c !== "|" ? "-" : c)).join("");
  separatorRowsIdxs.forEach(index => {
    outputTable.splice(index, 0, lineRow);
  });

  const selectedCell =
    rows[selectedRow].slice(0, selectedChar).split("|").length - 2;
  const resultCellsLength = outputTable[0].split("|").length - 2;

  let nextSelectedRow = selectedRow;
  let nextSelectedCell = selectedCell + 1;

  if (nextSelectedCell === resultCellsLength) {
    nextSelectedCell = 0;
    do {
      nextSelectedRow += 1;
    } while (separatorRowsIdxs.includes(nextSelectedRow));

    if (!outputTable[nextSelectedRow]) {
      nextSelectedRow = 0;
    }
  }

  return [outputTable, nextSelectedRow, nextSelectedCell];
}

function padBothToLength(str, toLength, padStr = " ") {
  const fromLength = str.length;
  const fromStart = Math.floor((toLength - fromLength) / 2);
  const fromEnd = Math.ceil((toLength - fromLength) / 2);

  return `${padStr.repeat(fromStart)}${str}${padStr.repeat(fromEnd)}`;
}

// =============================================================================
// Headers navigation

function selectHeader(prop, state, dispatch, view) {
  const { $from, to } = state.selection;
  const same = $from.sharedDepth(to);
  if (same == 0) return false;

  const proseNode = $from.before(same);
  let currentNode = view.nodeDOM(proseNode);

  while (!currentNode.parentNode.classList.contains("ProseMirror")) {
    currentNode = currentNode.parentNode;
  }

  let next = currentNode;
  while ((next = next[prop])) {
    if (next.classList.contains("heading")) {
      next.scrollIntoView({
        block: "start",
        inline: "nearest",
        behavior: "smooth"
      });
      const pos = view.posAtDOM(next);
      dispatch(
        view.state.tr.setSelection(
          Selection.near(view.state.doc.resolve(pos + next.tagName[1] * 1))
        )
      );
      return true;
    }
  }
}

export function selectNextHeader(...args) {
  return selectHeader("nextElementSibling", ...args);
}

export function selectPrevHeader(...args) {
  return selectHeader("previousElementSibling", ...args);
}
